//@use "bootstrap-utilities";

@use 'components/bootstrap';

@use 'components/gerillass';

@use 'helpers' as *;



html
{
    font-size: 16px;
    line-height: 1.2;
}


body
{
    min-height: 100vw;
    margin: 0;

    font-size: gerillass.remify(17px);




}


*,
::before,
::after
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}


a
{
    text-decoration: none;
}


/* Text meant only for screen readers. */
.screen-reader-text
{
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    word-wrap: normal !important;
}


.nav-pills
{
    position: fixed;
    right: 2vw;
    z-index: 10;
    top: 50vh;
    transform: translateY(-50%);

    ul
    {
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-items: center;

        li
        {
            margin: .5rem 0;
            cursor: pointer;

            a
            {

                border: 2px solid;
                border-radius: 50%;
                display: block;

                outline: 0;
                border-color: rgba(123, 123, 123, .77);
                padding: 3px;
            }

            &.active
            {
                a
                {
                    border-color: rgba(202, 63, 63, 0.94);
                    padding: 5px;
                }

            }
        }
    }
}

.page
{
    min-height: 100vh;
}



/* Landing Page */

#home
{
    background: $home-bg;
    color: $home-color;
}